import React from 'react'
import { Container, Row, Col} from 'react-bootstrap'
import Header from './Header'
import ProductComponent from './ProductComponent'
import Footer from './Footer'
import Headroom from 'react-headroom'
function ShopCoffee() {
  return (
    <>
        <Headroom>
        <Header/>
        </Headroom> 
      <section className='my-5'>
        <Container>
            <Row>
                <Col>
                  <div className='border bg-box p-5'>
                   <h2 className='mt-2 mb-3'>Shop Coffee</h2>
                   <p>Coffee is a brewed drink prepared from roasted coffee beans, the seeds of berries from certain Coffea species. When coffee berries turn from green to bright red in color  indicating ripeness  they are picked, processed, and dried.[2] Dried coffee seeds (referred to as "beans") are roasted to varying degrees, depending on the desired flavor.</p>
                  </div>
                </Col>
                <Col>
                  <div className='border box p-5 bg-img-box'>
                  </div>
                </Col>
            </Row>
        </Container>
      </section>

      <section className='my-5'>
        <ProductComponent/>
      </section>

      <Footer/>
    </>
  )
}

export default ShopCoffee