import React from 'react'
import Header from './Header'
import {Carousel,Container,Row,Col}from 'react-bootstrap';
import Btn from './Btn';
import Product from './Product';
import Card from './Card';
import ProductComponent from './ProductComponent';
import Carouselimg from '../image/zpstock-image-961.jpg'
import Carousel_img from '../image/zpstock-image-962.jpg'
import CarouselImg from '../image/zpstock-image-963.jpg'
import ReadMoreBtn from './ReadMoreBtn';
import ThirdBtn from './ThirdBtn';
import Footer from './Footer';
import img4   from  '../image/zpstock-image-965.png'
import Img from '../image/zpstock-image-964.png'
import Img2 from '../image/zpstock-image-966.png'
import PopUp from './Popup';
import Headroom from 'react-headroom';
import SignInHeader from './SignInHeader';
function Home(props) {

  return (
    <>
    <Headroom>
      <SignInHeader/>
    <Header/>
    </Headroom>
    <Carousel variant="dark">
      <Carousel.Item>
        <img
          className="d-block blur-bg w-100"
          height='400px'
          style={{height:"400px !imporatant"}}
          src={Carouselimg}
          alt="First slide"
        />
      <div className='text-center text-warning cotent-management'>
       <h1 className='mb-4 text-white'>ARTISAN ROASTED COFFEE</h1>
       <p className='text-center text-white text-query'>Begin your day with the great flavor of our specialty roasts. Our flagship blend combines sweet golden marmalade and the bright acidity of red apple.</p>
       <ReadMoreBtn name={'SHOP NOW'}/>
      </div>
      </Carousel.Item>
      <Carousel.Item >
        <img
          className="d-block w-100 blur-bg"
          height='400px'
          style={{height:"400px !imporatant"}}
          src={Carousel_img}
          alt="First slide"
        />
      <div className='text-center text-warning cotent-management'>
       <h1 className='mb-4 text-white'>LEAFING THROUGH HISTORY</h1>
       <p className='text-center text-white text-query'>Our tea evangelist takes you on a journey to find some of the worlds most extraordinary teas. Meet Jane, the force behind the Artisan Tea Shop.</p>
       <ReadMoreBtn name={'READ MORE'||'btn-bg'}/>
      </div>
      </Carousel.Item>
      <Carousel.Item >
        <img
          className="d-block w-100 blur-bg"
          height='400px'
          style={{height:"400px !imporatant"}}
          src={CarouselImg}
          alt="First slide"
        />
      <div className='cotent'>
       <h1 className='text-danger mb-4 h-status'>THE FINEST TEAS</h1>
       <p className='blue text-query-1'>Get enchanted by the masterful and the curious. Our team works hard to source the highest quality blends of tea leaves for you. Immerse yourself in the world of specialty teas.</p>
       <ThirdBtn btnname={'SHOP TEA'} />
      </div>
      </Carousel.Item>
    </Carousel>
   <section className='bg-s py-5'>
   <h2 className='text-center zpheading-style-type blue'>Crafting the future of coffee.</h2>
   <Container className='row-margin'>
      <Row className=''>
        <div className='col-4  text-center'>
        <img width={'100'} alt='' src='https://static.zohocdn.com/sites/stock-images/images/zpstock-image-967.png'/>
        <h3 className='mt-3 blue'>Coffee delivered</h3>
        <p className='mt-4 blue'>On your schedule. On time, every time.</p>
        </div>
        <div className='col-4 text-center'>
        <img width={'100'} alt='' src='https://static.zohocdn.com/sites/stock-images/images/zpstock-image-968.png'/>
        <h3 className='mt-3 blue'>Craft coffee</h3>
        <p className='mt-4 blue'>On your schedule. On time, every time.</p>
        </div>
        <div className='col-4 text-center'>
        <img width={'100'} alt='' src='https://static.zohocdn.com/sites/stock-images/images/zpstock-image-969.png'/>
        <h3 className='mt-3 blue'>Award-winning</h3>
        <p className='mt-4 blue'>On your schedule. On time, every time.</p>
        </div>
      </Row>
     <Btn/>
    </Container>
   </section>


    <section className='mt-5'>
    <Container>
    <h2 className='blue'>All Products</h2>
    <div className='mt-5'>
    <Row>
    <Product/>
    </Row>


      </div>
    </Container>
    </section>



    <section className='my-5 bg-s py-5'>
    <Container>
     <Row>
      <Col>
      <div className='p-3'>
      <div style={{height:'150px'}}></div>
      <h2 className='blue'>COFFEE WITH INTEGRITY</h2>
      <p className='mt-4 blue'>We are committed to sourcing each bean sustainably keeping the health of our planet and our farmers in mind. Our Artisan Roasted Coffee is made in small batches and handcrafted to maintain the quality, aroma,and character of each bean.</p>
      <p className='mt-4 blue fs-5'>We are RAINFOREST ALLIANCE and FAIR TRADE certified.</p>
      <ThirdBtn btnname={'LEARN MORE' || 'btnclass'} />
      </div>
      </Col>
      <Col className='in-responsive-view'>

      <Card imgchange={Img}/>

      <Card imgchange={img4}/>

      <Card imgchange={Img2}/>

      </Col>
     </Row>
    </Container>
    </section>


    <section className='mt-5'>
    <Container>
    <h2 className='blue'>Best Sellers</h2>
    <ProductComponent/>
    </Container>
    </section>


<section className='mt-5 image_wrapper'>

    <img
          className="d-block blur-bg w-100"
          height='270px'
          src={Carouselimg}
          alt=""
        />
      <div className='text-center text-white overlay overlay_3 pt-5'>
       <h3 className='mb-4 mt-4'>GET UPDATES AND DEALS FROM ARTISAN ROASTERS</h3>
       <p className='text-center '>Submit your email and receive special offers, brewing tips, new blend announcements, and $4.99 off your first order!</p>
       <form className="d-flex justify-content-center">
            <input
              className='p-2 bg-search me-2'
              type="email"
              placeholder="Email"
              aria-label="email"
            />
            <button className=' bg-subscribe' variant="">Subscribe</button>
          </form>

      </div>
    </section>
 <Footer/>

    </>
  )
}

export default Home