import React,{ useState} from 'react'
import '../App.css'
import { Link } from 'react-router-dom'


const isToken= localStorage.getItem('token');
const Log = () =>{

const  user = localStorage.getItem('first_name')

  if (isToken === null) {
    return(
    <div className='py-2'><Link to={'/signin'} className='text-white text-decoration-none me-4'>Sign In</Link>
    <Link to={'/signup'} className='text-white text-decoration-none ms-2'>Sign Up</Link></div>
    )
   } else {
    return(
      <div className='d-flex'>
      <li class="nav-item list-unstyled dropdown">
        <Link class="nav-link dropdown-toggle text-white me-4 py-2" to={''} id="navbarDropdown" role="button"  aria-expanded="false">
            Hii,{user}
          </Link>
          <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><Link class="dropdown-item" to={'/'}>My Profile</Link></li>
            <li><Link class="dropdown-item" to={'/'}>My  Orders</Link></li>
            <li><hr class="dropdown-divider"/></li>
            <li><Link class="dropdown-item" to={'/'}>Address Book</Link></li>
          </ul>
          </li>
          <li class="nav-item list-unstyled dropdown py-2">
      <Link className='text-white text-decoration-none' onClick={()=>{localStorage.clear()}} to={'/signin'}>Logout</Link>
      </li>
      </div>

    )
  }
}

function SignInHeader() {

  return (
    <div className='bg_header'>
      <div className='content-position-end w-sm'>
      <Log/> 
      </div>
    </div>
  )
}

export default SignInHeader