import React,{useState,useEffect} from 'react';
import Button from 'react-bootstrap/Button';
import '../App.css'
import axios  from '../api/axios';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import Header from './Header';
import Headroom from 'react-headroom';
function ProductDetails() {

  const post_URL= 'api/blog/product';


    const [product_name ,setproduct_name] = useState('');
    const [product_price ,setproduct_price] = useState('');
    const [product_pic,SetPfpic]=useState('')
  
    const navigate =useNavigate();
   

    
  const submitHandler = async(e) =>{
   e.preventDefault();

  try {
    const formData = new FormData();
     formData.append('product_name', product_name);
     formData.append('product_price', product_price);

     formData.append('product_pic',product_pic);
  
  const response = await axios.post(post_URL ,formData
     );
     
  console.log(response)  ;
  navigate('/')

    
  } catch (error) {
    toast.error(error.response.data.message);
    console.log(error.response.data.message);
    
  }
  
  }




  return (
    <div>
        <Headroom>
        <Header/>
        </Headroom>
    <div className='container bg-light border shadow rounded pb-4 mt-5'>
      <form onSubmit={submitHandler}>
       <div className='text-center text-danger fs-1 pt-1 border-1 border-bottom border-warning'>Ragistration Form</div>

       <div className='mt-5 mx-5'>
       <div className='mb-3 row'>
        <div className='fs-5 col-3'>Product Name :</div>
        <div className='col-9'>
        <input
         onChange={(e)=>setproduct_name(e.target.value)}
         type={'text'} 
         name='username'
         placeholder='Username'
         className='form-control'
          
        />
         </div>
       </div>

       <div className='mb-3 row'>
        <div className='fs-5 col-3'>Product price :</div>
        <div className='col-9'>
          <input 
          type={'text'}
          onChange={(e)=>setproduct_price(e.target.value)}
         name='Product price'
         placeholder='Product price' 
         className='form-control'          
         /></div>
       </div>

       <div className='mb-3 row'>
          <div className='fs-5 col-3'>product pic</div>
          <div className='col-9'><input 
           type={'file'}
           onChange={(e)=>SetPfpic(e.target.files[0])}
           placeholder='Product pic' 
           className='form-control'

           /></div>
         </div>

       <div className='mt-4'>
       <Button className='me-2 space fw-bolder px-5 fs-5' type='submit' variant="success">Submit</Button>
       </div>

       </div>
       </form>
       </div>
    </div>
  )
}

export default ProductDetails