import React, { Component } from 'react';
import {
  BrowserRouter,
  Routes,
  Route,
  Outlet,
  Navigate
} from 'react-router-dom';
import AddToCart from './components/AddToCart';
import Contact from './components/Contact';
import Home from './components/Home';
import PageNotfound from './components/PageNotfound';
import Product from './components/Product';
import ProductDetails from './components/ProductDetails';
import ShopCoffee from './components/ShopCoffee';
import ShopTea from './components/ShopTea';
import TearmsAndCondition from './components/TearmsAndCondition';
import { ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ViewDetails from './components/ViewDetails';
import SignIn from './components/SignIn';
import Signup from './components/Signup';
import Userdata from './components/Userdata';
import EditUser from './components/EditUser'

const Private = () =>{
  const token =  !!localStorage.getItem('token');
  return(
    token? <Outlet/> : <Navigate to="/signin"/>)
  }

export class App extends Component {
  render(){
  return (
     <BrowserRouter>
     <Routes>
      <Route path='/' element= {<Home/>}></Route>

      <Route path='/signin' element= {<SignIn/>}></Route>

      <Route path='/signup' element= {<Signup/>}></Route>

      <Route path='/shopcoffee' element= {<ShopCoffee/>}></Route>

      <Route path='/shoptea' element= {<ShopTea/>}></Route>

      <Route path='/contact' element= {<Contact/>}></Route>

      <Route path='/tearmsandcondition' element= {<TearmsAndCondition/>}></Route>

      <Route path='/product' element={<Private/>}>
      <Route path='/product' element= {<Product/>}/></Route>

      <Route path='/product' element={<Private/>}>
      <Route path='/productdetails' element= {<ProductDetails/>}/>
      </Route>

      <Route path='/addtocart' element= {<AddToCart/>}></Route>

      <Route path='/viewdetails' element= {<ViewDetails/>}></Route>

      <Route path='/userdata' element={<Private/>}>
      <Route path='/userdata' element= {<Userdata/>}/></Route>

      <Route path='/edituser' element={<Private/>}>
      <Route path='/edituser' element= {<EditUser/>}/></Route>
      
      <Route path='*' element= {<PageNotfound/>}></Route>
   
     </Routes>
     <ToastContainer/>
     </BrowserRouter>
  )
}
}

export default App ;