import React,{useState,useEffect} from 'react';
import Header from './Header'
import Button from 'react-bootstrap/Button';
import axios from '../api/axios';
import  {Link} from "react-router-dom"
import Headroom from 'react-headroom';
import Footer from './Footer'
const data_URL= 'api/user/getalluser';

const Ddata_URL= 'api/user/delete';

function Userdata() {
 
  const [alldata, setAlldata] = useState([]);
  const [update , isUpdate]= useState('false');

  useEffect(() => {
  const getdata = async () =>{
    try {
      const response = await axios.get(data_URL)  
      const alluser = response.data;
            console.log(alluser)

      setAlldata(alluser);
    } catch (error) {
      console.log(error.response.data.message);
    }

  }

  getdata()

},[update])


  const deleteHandler = async (id) => {
  console.log(id);
    try {
      const response = await axios.delete(`${Ddata_URL}/${id}`)
      console.log("deleted successfully!")
      isUpdate(!update);
      
    } catch (error) {
      console.log("Something went wrong", error)
    }
  }



  return (
    <div>
        <Headroom>
        <Header/>
        </Headroom>

        <div className='container my-5'>
        <div className='text-center text-danger border-1 border-bottom border-warning fs-1 my-3'>User Data</div>

        <div className='row mx-5'>
         <table className='table'>
           <tbody>
            
             <tr>
          <th>Firstname</th>
          <th>Lastname</th>
          <th>Contact</th>
          <th>Email</th>
          <th>Action</th>
          
        </tr>

        {
          alldata?.map((user,i)=>{
         return(     
        <tr key={i}>
          <td>{user?.first_name}</td>
          <td>{user?.last_name}</td>
          <td>{user?.phone}</td>
          <td>{user?.email}</td>
          <td>
          <Link to={`/edituser/${user._id}`}><Button className='me-2' type='edit' variant=""><i className="fa-solid fa-pen-to-square"></i></Button></Link>
          <Button className='me-2' onClick={()=>{deleteHandler(user._id)}} type='reset' value={'reset'} variant=""><i className="fa-solid fa-trash text-danger"></i></Button>
          </td>
        </tr>
                
                )
              })
              }  


        </tbody>

                  </table>
                </div>


        </div>
    <Footer/>
    </div>



  )
}

export default Userdata