import React,{useState,useEffect} from 'react';
import { Col, Container, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import '../App.css'
import Button from 'react-bootstrap/Button';
import axios from '../api/axios';
const postdata_URL= 'api/blog/getallproduct';

const pDdata_URL= 'api/blog/deleteproduct';
function Product() {
    const [allpost, setAllpost] = useState([]);
    const [update , isUpdate]= useState('false');
  
  
    
    useEffect(() => {
      const getpost = async () =>{
        try {
          const response = await axios.get(postdata_URL)  
          const allpost = response.data; 

          setAllpost(allpost);

        } catch (error) {
          console.log(error.response.data.message);
        }
    
      }
      getpost()
    
    },[])
  
  
      
    const deleteHandler = async (id) => {
  
      try {
      const response = await axios.delete(`${pDdata_URL}/${id}`)
      
      console.log("deleted successfully!")
      isUpdate(!update);

      localStorage.setItem("product_name", response.data.product_name);
      localStorage.setItem("product_price", response.data.product_price);
      localStorage.setItem("product_pic_url", response.data.product_pic_url);
      localStorage.setItem("productid", response.data.product._id);
      
    } catch (error) {
  
      console.log("Something went wrong", error)
    }
  }

  return (
    <>
    <Container className='mt-5'>
        <Row>
        

        {
          allpost?.map((post,i)=>{
         return( 
          <Col md='4' sm='12' xl='4' className='text-center' key={i}>
          <div className='image_wrapper'>
          <img alt='' className='border shadow mb-3' src ={post?.product_pic_url}/>
          <div className='overlay_4'>
           <Link to={'/viewdetails'}><button className='btnbg-color'>View Details</button></Link>
           <div className='mt-3'>
             <Link to={'/'}> <i className="fa fa-magnifying-glass text-white"></i></Link>
              <hr className='text-white'></hr>
           </div>
          </div>

          </div>
      <Link to={'/'}><label className='text-look mb-3'>{post?.product_name}</label></Link>
      <div className='blue mb-3'>{post?.product_price}<span className='theme-prod-old-price'></span></div>
      <div className='d-flex ml'>
      <i className="fa-sharp rating fa-solid fa-star"></i>
      <i className="fa-sharp rating fa-solid fa-star"></i>
      <i className="fa-sharp rating fa-solid fa-star"></i>
      <i className="fa-sharp rating fa-solid fa-star"></i>
      <i className="fa-sharp rating fa-regular fa-star-half-stroke"></i>
      </div>
      </Col>
                )
                   })
              }  


        </Row>
    </Container>
    </>
    )
}

export default Product