import React from 'react';
import { Button, Container} from 'react-bootstrap'
import Footer from './Footer'
import HeaderSecond from './HeaderSecond'
import img from '../image/cart.png'
import '../App.css'
function AddToCart() {

  return (
    <>
      <HeaderSecond />


<Container>

<div className='my-5'>
  <table  className='table'>
    <tbody>
      <tr>
        <th>Item</th>
        <th>Price</th>
        <th>Quantity</th>
        <th colSpan={'2'}>Subtotal</th>
        <th>Remove</th>
      </tr>

      <tr>
        <td className='d-flex'>
          <img 
          alt=''
          width={'200'} 
          height={'100'}
          src={img}
          />
          <div className='text-look ms-4 mt-4'>Moka Pot</div>
          </td>
          <td >
          <div className='mt-4'>Rs. 19.99</div>
        </td>
        <td colSpan={'2'}>
          <input
           type='number'
           name='quantity'
           id='quantity'
           className='border py-2 ps-3 me-3 mt-3'
           />
        </td>
        <td colSpan={'1'}><div className='mt-4' >Rs. 39.98</div></td>
        <td>
          <Button variant='' className='text-danger mt-3'><i class="fa-regular fa-trash-can"></i></Button>
        </td>
      </tr>

  </tbody>

  </table>
  <Button href='/' className='mt-3 btn-bg-color'>Continue Shopping</Button>
  <Button className='mt-3 btnclass float-end'>Checkout</Button>
</div>

</Container>


<Footer />
    </>
  )
}

export default AddToCart