import React,{useState,useEffect} from 'react';
import { Button } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom';
import axios  from '../api/axios';
import { toast } from 'react-toastify';

const Login_URL= 'api/user/login';


function SignIn() {

  
  const [email ,setEmail] = useState('');
  const [password ,setPassword] = useState('');
  const navigate =useNavigate();


  
const loginHandler = async (e) => {

  e.preventDefault()


  try {

      const response = await axios.post(Login_URL ,{
          email:email,
          password:password,

        });

        console.log(response);
        localStorage.setItem("token", response.data.token)
        localStorage.setItem("userid", response.data.user._id)
        navigate('/')

  } catch (error) {
      toast.error(error.response.data.message);
      console.log(error.response.data.message);

  }

}


  return (
    <div>
      <form onSubmit={loginHandler}>
        <h4 className='mt-4 text-center blue'>BLACK SLATE</h4>
        <div className='cotent-manage'>
          <div className='fs-5 mb-2 blue'>Sign in</div>
          <div className='blue'>to access BLACK SLATE</div>
          <input 
        type={'email'}
        onChange={(e)=>setEmail(e.target.value)}
        name='email' 
        placeholder='Email' 
        className='form-control my-3'
        />
          <input
         type={'password'}
         onChange={(e)=>setPassword(e.target.value)}
         name='password' 
         placeholder='Password' 
         className='form-control'
         />
          <Button variant='' type='submit' className='float-end my-3 btnclass'>
            Sign In 
          </Button>
          <div className='mt-5'>Don't have an account?<Link className='ms-2 text-decoration-none' to={'/signup'}><span className='text-color-info'>Sign Up</span></Link></div>
          <Link className='mt-2 text-decoration-none' to={'/signup'}><span className='text-color-info'>Forgot Password</span></Link>
        </div></form> 
    </div>
  )
}

export default SignIn