import React from 'react'
import { Button, Form, Nav } from 'react-bootstrap';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'react-router-dom';
import '../App.css'

function HeaderSecond() {
  return (
    <div>
      <Navbar expand="lg" className='Header-bg py-4 shadow'>
        <Container>
        <Navbar.Brand href="#" className='fs-3 fw-light'>BLACK SLATE</Navbar.Brand>
        <Nav>
        <Form className='d-flex'>
            <Form.Control
                    type="search"
                    placeholder="Search"
                    aria-label="Search"
                  />
              <button type='search' className='btnclass px-3'><i className="fa fa-magnifying-glass"></i></button>
                </Form>
              <Link to={'/addtocart'}><Button variant='' className=' ms-3 fs-5'><i class="fa fa-light fa-cart-shopping"></i></Button></Link>

        </Nav>
        </Container>
      </Navbar>

    </div>
  )
}

export default HeaderSecond