import React,{useState,useEffect} from 'react';
import Button from 'react-bootstrap/Button';
import '../App.css'
import axios  from '../api/axios';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { Form } from 'react-bootstrap';
import { toast } from 'react-toastify';

const data_URLfind= 'api/user/getuserbyid';

const data_URLedit= 'api/user/edituser';


function EditUser() {

    const navigate =useNavigate();

    const [alldata, setAlldata] = useState([]);
    const [update , isUpdate]= useState('false');
     const {id} = useParams();
    useEffect(() => {
    const getdata = async () =>{
      try {
        const response = await axios.get(`${data_URLfind}/${id}`)  
        const alluser = response.data;  
        console.log(alluser)
        setAlldata(alluser);
      } catch (error) {
        console.log(error.response.data.message);
      }
  
    }
  
    getdata()
  
  },[update]);
 

  console.log(alldata);

const  EditHandler = async(e) =>{
  e.preventDefault();
 try{
  const response = await axios.patch(`${data_URLedit}/${id}` ,{
  email:alldata.email,
  password:alldata.password,
  first_name:alldata.first_name,
  last_name:alldata.last_name,
  phone:alldata.phone,
  
});
console.log(response)  ;
navigate('/formdetails')
  
} catch (error) {
  toast.error(error.response.data.message);
  console.log(error.response.data.message);
  
}
}

return (
  <div>
  <h4 className='mt-4 text-center blue'>BLACK SLATE</h4>
  <form onSubmit={EditHandler}>
  <div className='cotent-manage'>
  <div className='fs-5 mb-2 blue'>Sign Up</div>
  <div className='mb-3'>
  <label className='blue'>Email<span className='text-danger ms-1 fw-bold'>*</span></label>
  <input 
   onChange={(e)=>setAlldata({...alldata,email:e.target.value})}
   id='email'
   name='email'
   placeholder='Email'
   className='form-control mt-2'
   value={alldata?.email} 
  />
  </div>
  <div className='mb-3'>
  <label className='blue'>Password<span className='text-danger ms-1 fw-bold'>*</span></label>
  <input 
   onChange={(e)=>setAlldata({...alldata,password:e.target.value})}
   id='password'
   name='password'
   placeholder='Password'
   className='form-control mt-2'
   value={alldata?.password} 
  />
  </div>
  <div className='mb-3'>
  <label className='blue'>First Name<span className='text-danger ms-1 fw-bold'>*</span></label>
  <input 
   onChange={(e)=>setAlldata({...alldata,first_name:e.target.value})}
   id='name'
   name='name'
   placeholder='First Name'
   className='form-control mt-2'
   value={alldata?.first_name} 
  />
  </div>
  <div className='mb-3'>
  <label className='blue'>Last Name<span className='text-danger ms-1 fw-bold'>*</span></label>
  <input 
   onChange={(e)=>setAlldata({...alldata,last_name:e.target.value})}
   id='name'
   name='name'
   placeholder='Last Name'
   className='form-control mt-2'
   value={alldata?.last_name} 
  />
  </div>
  <div className='mb-3'>
  <label className='blue'>Phone<span className='text-danger ms-1 fw-bold'>*</span></label>
  <input 
   onChange={(e)=>setAlldata({...alldata,phone:e.target.value})}
   id='number'
   name='number'
   placeholder='Phone'
   className='form-control mt-2'
   value={alldata?.phone} 
  />
  </div>
  <Form className='d-flex'>
  <Form.Check aria-label="option 1"/><p className='ms-2 blue'>Subscribe to our newsletter for our latest news and updates.</p>
  </Form>
  <Button variant='' type='submit' className='float-end my-2 btnclass'>Sign Up</Button>
      <div className='mt-3'>Already have an account?<Link className='ms-2 text-decoration-none' to={'/signin'}><span className='text-color-info'>Sign In</span></Link></div>
  </div>
  </form>
</div>
  )
}

export default EditUser