import React from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap'
import Footer from './Footer'
import Header from './Header'
import Img_1 from '../image/400x400.png'
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Sonnet from './Sonnet'
import ProductComponent from './ProductComponent'
import Headroom from 'react-headroom'

function ViewDetails() {

const  product_name = localStorage.getItem('product_name')

  return (
    <>
        <Headroom>
        <Header/>
        </Headroom>
      <div className='my-5'>
      <Container>
        <Row className='border shadow'>
            <Col md='4' className='text-white'>
              <img
                src={Img_1}
                className='my-5 w-100'
              />
            </Col>
            <Col md='5' className='bg-section p-5'>
             <h3 className='text-white'>{product_name}</h3>
             <div className='d-flex mt-3'>
            <i className="fa-sharp rating fa-solid fa-star "></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp text-white fa-solid fa-star ms-1"></i>
            <span className='ms-3 text-white'>1 Review | Write a review</span>
            </div>
            <hr className='text-white'/> 
            <div className='text-white'>Select Capacity</div>
            <div className='d-flex mt-3'>
            <button className='me-2 border px-3 py-1 btn-site'>250 g</button>
            <button className='me-2 border px-3 py-1 btn-site'>500 g</button>
            <button className='me-2 border px-3 py-1 btn-site'>750 g</button>
            <button className='me-2 border px-3 py-1 btn-site'>1 kg</button>
            </div>
            <hr className='text-white'/>
            <p className='text-white my-4 fs-class'>Use this paragraph to describe your product. Add information that your users may find interesting. You can edit text on your website by double clicking on it. Try now.</p>
            <hr className='text-white'/>
            <div className='d-flex'>
            <i class="fab fa-facebook-f fs-3 text-primary me-5"></i>  
            <i class="fab fa-twitter fs-3 text-primary"></i> 
            <i class="fab fa-pinterest-p fs-3 text-danger ms-5"></i>
            </div>
            </Col>
            <Col md='3' className='bgsection'>
             <h4 className='text-white my-5 ms-3'>Rs.2,500.00</h4>
             <hr className='text-white'/>
             <div className='text-white fs-class mt-3'>Message</div>
             <div className='text-white fs-class mt-2'>Free Service for One Year</div>
             <div className='text-white fs-class mt-4'>Seller</div>
             <div className='text-white fs-class mt-2'>Indian Coffee</div>
             <hr className='text-white'/>

             <h4 className='text-white fw-light my-4'>Choose Quantity</h4>

             <input
                   type='number'
                   name='quantity'
                   id='quantity'
                   className='form-control'
                   />

                <Button className='mt-3 btnclass w-100'>Add To Cart</Button>

            </Col>
        </Row>

        <section className='my-5 border shadow p-4 rounded'>
        <Tabs
      defaultActiveKey="profile"
      id="uncontrolled-tab-example"
      className="mb-3"
    >
      <Tab eventKey="Product Details" title="Product Details">
        <Sonnet />
      </Tab>
      <Tab eventKey="Specifications" title="Specifications">
         <div className='fs-5 my-4 ms-2' >General</div>
         <table className='table'>
            <tbody>
         <tr>
         <th>Material</th>
         <th>Brand</th>
         <th>Colour</th>
         <th>Capacity</th>
         <th>Item Weight</th>
         </tr>

         <tr>
         <td className=''>Aluminum</td>
         <td className=''>4G Creation</td>
         <td className=''>Silver</td>
         <td className=''>150 Mlit</td>
         <td className=''>250 Kg</td>
         </tr>
         </tbody>
         </table>

      </Tab>
    </Tabs>

        </section>

 
 <section className='my-5'>
    <h4 className='my-3'>Ratings</h4>
    <i className="fa-sharp rating fa-solid fa-star "></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp text-lite fa-solid fa-star ms-1"></i>
            <div>4.0 / 5 (1 Rating)</div>

            <h4 className='my-3'>Review</h4>
    <i className="fa-sharp rating fa-solid fa-star "></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp rating fa-solid fa-star ms-1"></i>
            <i className="fa-sharp text-lite fa-solid fa-star ms-1"></i>
            <div className='my-3'>Saravanan 01.02.21 04:58 AM</div>
            <p className='fs-class blue'>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec suscipit quam a lorem luctus rhoncus.</p>
            <hr className='blue'/>

            <div className='text-info fs-class'>Login to write a review.</div>
 </section>

 <section className='my-5'>
    <h3>Recommended Products</h3>
    <ProductComponent/>
    <ProductComponent/>
 </section>


      </Container>
    </div>
    <Footer/>
    </>
  )
}

export default ViewDetails