import React from 'react'
import { Col, Row } from 'react-bootstrap'
function Card(props) {
    console.log(props.imgchange);
  return (
   <>
    <div className='border box card shadow p-4 mb-3'>
    <Row>
        <Col md='3' xl='3' xxl='3'> 
            <div>
        <div className='border card p-3'>
        <img src={props.imgchange} alt=''/>
        </div>
        </div>
        </Col>
        <Col md='9' xl='9' xxl='9'>
        <h3 className='fw-light blue'>Craft Coffee</h3>
        <p className='blue '>We source the world's finest coffee. We honor our commitment to you with a team dedicated to their craft.</p>
        </Col>
    </Row>
    </div>
   </>
    )
}

export default Card