import React,{useState,useEffect} from 'react';
import { Button, FormCheck } from 'react-bootstrap'
import { Form } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import axios  from '../api/axios';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

const Add_URL= 'api/user/signup';


function Signup() {


  const [first_name ,setfirst_name] = useState('');
  const [last_name ,setlast_name] = useState('');
  const [contact ,setcontact] = useState('');
  const [email ,setEmail] = useState('');
  const [password ,setPassword] = useState('');

  const navigate =useNavigate();


  const submitHandler = async(e) =>{
  e.preventDefault();
   try {
   

   const response = await axios.post(Add_URL,
   { first_name:first_name,
    last_name:last_name,
    phone:contact,
    email:email,
    password:password }
      
      );
   console.log(response);
   navigate('/signin')
     
   } catch (error) {
     toast.error(error.response.data.message);
     console.log(error.response.data.message);
     
   }
   
   }



  return (
    <div>
      <h4 className='mt-4 text-center blue'>BLACK SLATE</h4>
      <form onSubmit={submitHandler}>
      <div className='cotent-manage'>
      <div className='fs-5 mb-2 blue'>Sign Up</div>
      <div className='mb-3'>
      <label className='blue'>Email<span className='text-danger ms-1 fw-bold'>*</span></label>
      <input 
       onChange={(e)=>setEmail(e.target.value)}
       id='email'
       name='email'
       placeholder='Email'
       className='form-control mt-2'
      />
      </div>
      <div className='mb-3'>
      <label className='blue'>Password<span className='text-danger ms-1 fw-bold'>*</span></label>
      <input 
       onChange={(e)=>setPassword(e.target.value)}
       id='password'
       name='password'
       placeholder='Password'
       className='form-control mt-2'
      />
      </div>
      <div className='mb-3'>
      <label className='blue'>First Name<span className='text-danger ms-1 fw-bold'>*</span></label>
      <input 
       onChange={(e)=>setfirst_name(e.target.value)}
       id='name'
       name='name'
       placeholder='First Name'
       className='form-control mt-2'
      />
      </div>
      <div className='mb-3'>
      <label className='blue'>Last Name<span className='text-danger ms-1 fw-bold'>*</span></label>
      <input 
       onChange={(e)=>setlast_name(e.target.value)}
       id='name'
       name='name'
       placeholder='Last Name'
       className='form-control mt-2'
      />
      </div>
      <div className='mb-3'>
      <label className='blue'>Phone<span className='text-danger ms-1 fw-bold'>*</span></label>
      <input 
       onChange={(e)=>setcontact(e.target.value)}
       id='number'
       name='number'
       placeholder='Phone'
       className='form-control mt-2'
      />
      </div>
      <Form className='d-flex'>
      <Form.Check aria-label="option 1"/><p className='ms-2 blue'>Subscribe to our newsletter for our latest news and updates.</p>
      </Form>
      <Button variant='' type='submit' className='float-end my-2 btnclass'>Sign Up</Button>
          <div className='mt-3'>Already have an account?<Link className='ms-2 text-decoration-none' to={'/signin'}><span className='text-color-info'>Sign In</span></Link></div>
      </div>
      </form>
    </div>
  )
}

export default Signup